.banner__form {
  display: flex;
  flex-direction: column;
  align-items: center;
  background: #fff;
  padding: 20px;
  border-radius: 5px;
  box-shadow: 0 7px 10px #ccc;
  align-self: flex-start;
  width: 260px;
  margin: auto;
  @include mobile-middle{
    align-self: center;
  }

  @include landscape{
    margin: 0;
  }
  .form__title {
    .title {
      color: $form__color-title;
      font-size: $form__title;
      font-family: $bold__roboto;
      text-align: center;
      margin: 0;
      letter-spacing: 2px;
    }
  }

  form{
    width: 100%;
    display: flex;
    flex-direction: column;
    .inputBlock{
      position: relative;
      padding: 10px 0;
      input{
        width: 100%;
        padding: 14px 0 10px 40px ;
        box-sizing: border-box;
        box-shadow: none;
        outline:none;
        border:1px solid rgba( $form__color-title, 0.4);
        border-radius: 7px;
        font-size: 16px;

      }
      ::-webkit-input-placeholder {padding-left: 10px;}
      ::-moz-placeholder          {padding-left: 10px;}/* Firefox 19+ */
      :-moz-placeholder           {padding-left: 10px;}/* Firefox 18- */
      :-ms-input-placeholder      {padding-left: 10px;}

      input::-webkit-outer-spin-button,
      input::-webkit-inner-spin-button {
        /* display: none; <- Crashes Chrome on hover */
        -webkit-appearance: none;
        margin: 0; /* <-- Apparently some margin are still there even though it's hidden */
      }

      label{
        position: absolute;
        top: 0px;
        pointer-events:none;
        left: 0;
        color:#999;
        transition:.5s;
        padding: 0.6em 10px;
        font-size: 28px;
        text-transform: uppercase;
        font-weight: 600;
        transition:.5s;
        opacity:1;
      }


    }

  }
}

.form__discont{
  width: 100%;
  display: flex;
  justify-content: space-between;
    padding: 10px 0;
  .discont,
  .actual{
    display: flex;
    flex-direction:column;
    div{
      height: 30px;
      display: flex;
      align-items: flex-end;
    }
  }

  .discont{
    color:$dark__purple;
    &__title{
      padding: 7px 0;
      font-size: $subtitle__size;
    }

    .old__price{
      font-size: $form__medium-size;
      background: url(../images/deleteicon.png);
      background-repeat: no-repeat;
      background-position: center;
      line-height: 0.8;
    }
    .currency{
      line-height: 0.7;
    }

  }

  .actual{
    &__title{
      padding: 7px 0;
    }

    &__price{
      color: $light__blue;
      font-family: $bold__roboto;
      font-size: $title__size;
      line-height: 0.8;
    }

    .currency{
      color: $light__blue;
      font-family: $bold__roboto;
    }
  }

}