body {
  min-width: 320px;
  font-size: 100%;
  color: #3b3944;
  background: #fff;
  margin: 0;
  padding: 0;
  font-family: $regular__roboto;
  @include mobile-less{
    overflow-x: hidden;
  }
}

.m-auto {
  margin: 0 auto;
}

.full-width {
  width: 100%;
}

.container {
  width: 100%;
  margin: 0 auto;
  @include mobile-landscape {
    max-width: 570px;
  }

  @include portrait {
    max-width: 720px;
  }

  @include landscape {
    max-width: 960px;
  }

  @include desktop {
    max-width: 1140px;
  }
}

.relative {
   position: relative;
 }

.absolute{
  position: absolute;
}

.z-index{
  z-index: 20;
}

.bg__blue {
  background-image: url(../images/background.png);
}

.uppercase{
  text-transform: uppercase;
}

.delimiter{
  position: absolute;
  width: 1px;
  height: 82%;
  background: $light__blue;
  left: 50%;
  opacity: 0.4;
}

.title {
  font-size: 2em;
  font-family: $bold__roboto;

  span {
    color: $form__color-title;
  }
}