.sendBtn{
 button{
   background: transparent;
   border: none;
   width: 100%;
   background-image: url(../images/button.png);
   background-position: center;
   background-repeat: no-repeat;
   background-size: cover;
   padding: 10px;
   font-size: 1.3em;
   text-transform: uppercase;
   color: #fff;
   font-family: $bold__roboto;
   letter-spacing: 1px;
   cursor: pointer;
   span{
     font-size: 14px;
     font-family: $regular__roboto;
   }
 }
}