@mixin mobile-landscape {
  @media (min-width: #{$mobile-landscape-width}) {
    @content;
  }
}




//tablet
@mixin portrait {
  @media (min-width: #{$tablet-portrait-width}) {
    @content;
  }
}

@mixin landscape {
  @media (min-width: #{$tablet-landscape-width}) {
    @content;
  }
}

//decktop
@mixin desktop {
  @media (min-width: #{$desktop-width}) {
    @content;
  }
}

@mixin desktop-hd {
  @media(min-width: #{$desktop-HD-width}) {
    @content;
  }
}

@mixin desktop-xhd {
  @media(min-width: #{$desktop-XHD-width}) {
    @content;
  }
}

@mixin desktop-xxhd {
  @media(min-width: #{$desktop-XXHD-width}) {
    @content;
  }
}

@mixin desktop-small-height {
  @media (max-height: #{$desktop-height}) {
    @content;
  }
}

@mixin desktop-height-xsmall {
  @media (min-height: #{$desktop-height-xsmall}) {
    @content;
  }
}

@mixin small-height {
  @media (max-height: #{$height}) {
    @content;
  }
}