.navigation__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logotype {
  display: flex;
  align-items: center;

  a {
    padding-left: $padding__small;
  }
}


.menu__block-button {
  display: block;
  padding-right: 10px;
  z-index: 1000;
  position: relative;
  &.active{
    position: fixed;
    right: 20px;
    top: 20px;
  }
  @include portrait {
    display: none;
  }

}

.navigation__list {

  display: none;
  &.active{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    min-height: 100vh;
    background: #f2f2f2;
    margin: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    z-index: 999;
    a{
      font-size: 2rem;
      color: $color__text;
      text-transform: uppercase;
      font-family: $bold__roboto;
      border-bottom: 4px dotted $form__color-title;
      &:hover{
        border-bottom: 4px solid $form__color-title;
              -webkit-transition: all 0.5s;
              -moz-transition: all 0.5s;
              -ms-transition: all 0.5s;
              -o-transition: all 0.5s;
              transition: all 0.5s;
      }
    }
  }


  @include portrait {
    display: block;
    position: relative;
  }

  li {
    padding: 20px 0;
    @include portrait {
      display: inline;
      padding-left: $padding__small;
      white-space: nowrap;
      a {

        @include portrait {
          color: $color__text;
          position: relative;
          padding-bottom: 5px;
          -webkit-transition: all 0.5s;
          -moz-transition: all 0.5s;
          -ms-transition: all 0.5s;
          -o-transition: all 0.5s;
          transition: all 0.5s;
  
          &:after {
            content: "";
            width: 100%;
            border-bottom: 2px dotted $color__text;
            position: absolute;
            left: 0;
            bottom: 0;
            -webkit-transition: all 0.5s;
            -moz-transition: all 0.5s;
            -ms-transition: all 0.5s;
            -o-transition: all 0.5s;
            transition: all 0.5s;
          }
  
          &:hover {
            &:after {
              border-bottom: 2px solid;
              -webkit-transition: all 0.5s;
              -moz-transition: all 0.5s;
              -ms-transition: all 0.5s;
              -o-transition: all 0.5s;
              transition: all 0.5s;
            }
          }
        }
        
      }
    }
  }
}