.card{
  display: flex;
  max-width: 470px;
  width: 100%;
  padding: $padding__small 0;

}
.card-icon{
  width: 50px;
  height: 50px;
  img{
    height: 100%;
  }
}

.card-body-title{
  color: #222;
  font-size: 16px;
}

.card-body{
  display: flex;
  flex-direction: column;
  padding-left: $padding__small;
  padding-right: 5px;
}

.card-body-description{
  padding-top: 15px;
  padding-right: 10px;
  color: #666;
}