.symptoms{
  padding: 20px 0 0 0;
  @include landscape{
    padding: 100px 0 0 0;
  }
  overflow: hidden;
  &__bg{
    background: url(../images/bg__symptoms.png);
    width: 100%;
    height: 100%;
    left: 50%;
    transform: translateX(-17%);
    z-index: -1;
    background-repeat: no-repeat;
  }
  &__title{
    text-align: center;
    font-family: $bold__roboto;
    font-size: $form__title;
    margin: auto;
    width: 85%;
    @include portrait{
      width: 60%;
    }

    padding-top:$padding__small;
    span{
      color: $tomato;
    }
  }

  &__block{
    padding-top: $padding__small;
    width: 100%;
    display: flex;
    justify-content: space-between;
    &-card{
      padding: 30px 0 0 10px;
    }
  }


}

.conclusion{
  padding-top: 40px;
  .card{
    &-icon{
      &:before{
        content: "";
        position: absolute;
        width: 100%;
        height: 2px;
        display: block;
        top: -20px;
        left: 0;
        background: #b9cfdd;
      }
    }

    &-body{
      &-title{
        &:before{
          content: "";
          position: absolute;
          width: 120px;
          height: 2px;
          display: block;
          top: -20px;
          background: #e5f2fa;
        }
      }
    }
  }
}

.symptoms__man{
  display: none;
  @include landscape{
    display: block;
  }
}
