.offer{
  &__background{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-repeat: no-repeat;
    background-size: cover;
  }

  &__banner{
    
    display: flex;
    padding: 150px 0px;
    flex-direction: column;
    align-items: center;
@include portrait{
  flex-direction: row;
  justify-content: space-between;
  padding: 150px 0px;
}


    .banner__text{
      width: 100%;
      max-width: 450px;
      font-size: $title__size;
      font-family: $bold__roboto;
      z-index: 1;

    .title{
      line-height: 1;
      font-size: 1em;
      padding-left: 10px;
      @include portrait{
        font-size: 1.5em;
        padding-left: 0;
      }
    }
      .subtitle{
        padding-top: $padding__small;
        padding-right: $padding__medium;
        padding-left: 10px;
        font-size: $subtitle__size;
        color: $color__subtitle;
        @include portrait{
          padding-left: 0;
        }
      }

      &-bullet{
        padding-left: 10px;
        @include portrait{
          padding-left: 0;
        }
        ul{
          display: flex;
          flex-direction: column;
          li{
            padding-top: $padding__small;
            font-size: $small__size;

            position: relative;
            display: inline;
            white-space: nowrap;

            &:before{
              content: "";
              position: absolute;
              width: 20px;
              height: 20px;
              border-radius: 50%;
              border: 1px solid $form__color-title;
              left: -40px;
              top: 50%;

            }

            &:after{
              content: "";
              position: absolute;
              width: 10px;
              height: 10px;
              border-radius: 50%;
              background: $form__color-title;
              left: -34px;
              top: 66%;
            }
          }
        }
      }
    }
  }
}

.banner__center{
  display: none;
  position: absolute;
  top: 0;
  height: 97%;
  left: 55%;
  bottom: 0;
  transform: translateX(-50%);
  z-index: 0;
  @include landscape{
    display: block;
  }
  .women{
    height: 100%;
    img{
      height: 100%;
    }
  }

  .boxes{
    bottom: 256px;
  }

}