.cream {
  top: 100px;

  &__block {
    display: flex;
    flex-direction: column;
    @include portrait {
      flex-direction: row;
      justify-content: space-between;
    }
  }

  &__bg {
    width: 100%;
    height: 100%;
    z-index: -1;

  }

  &__img {
    display: none;
    @include portrait {
      display: flex;
      justify-content: flex-end;
      width: 40%;
      @include landscape {
        width: 50%;
      }
    }

    .product {
      width: 100%;
      position: absolute;
      top: 50px;
      @include landscape {
        top: -40px;
      }
      img {
        width: 100%;
        @include desktop {
          width: 90%;
        }
      }
    }
  }
}

.cream__block-offer {

  padding: 50px 10px;

  .subtitle {
    padding: $padding__small 0;
    letter-spacing: 2px;
  }

  .sendBtn {
    width: 260px;
  }
}