.composition{
    overflow: hidden;
    top: 200px;
    .composition__block{
        display: flex;
        flex-wrap: wrap;
        flex-direction: column;
        @include portrait{
            flex-direction: row;
        }
    }
}

.composition__block-title,
.composition__block-extracts{
    @include portrait{
        width: 50%;
    }
    width: 100%;
}

.composition__block-title{
    display: flex;
    flex-direction: column;
    .title{
        @include portrait{
            width: 70%;
        }
        padding-left: 10px;
        width: 100%;
        font-size: 2.2em;
    }
}

.composition__block-img{
    display: none;
    padding-top: $padding__medium;
    @include portrait{
        display: block;
        width: 90%;
    }
    @include desktop{
        width: 100%;
    }
}


.composition__block-extracts{
    display: flex;
    flex-wrap: wrap;
    .extracts{
        @include mobile-middle{
            width: 50%;
        }
        width: 100%;
    }
}


.extracts__card{

    display: block;
    position: relative;
    display: block;
    position: relative;
    padding: 20px 60px 20px 20px;
    &-title{
            letter-spacing: 1px;
            position: relative;
            &:after{
                content: " ";
                width: 100%;
                height: 3px;
                left: 0;
                bottom: -15px;
                position: absolute;
                display: inline;
                background: rgba($light__blue,0.3);
                
            }
    }

    &-media{
        padding: 20px 0;
    }
    &-body{
        text-align: left;
        p{
            line-height: 1.4;
            color: #555;
        }
    }
}